export default {
	data() {
		return {
			StockEntity: {
				NewLocationCode: '',
				LocationCode: '',
				MaterialLabel: ''
			},
			labelFoucs: true,
			locationFoucs: false,
		}
	},
	methods: {
		getMaterialLabelInfo() {
			this.api.baseRequest({
				url:  'Stock/GetStockByMaterialLabel',
				method: "get",
				data: {
					MaterialLabel: this.StockEntity.MaterialLabel,
					LocationCode: this.StockEntity.LocationCode
				},
				success: res => {
					var resData = JSON.parse(res.data.Content)
					if (resData.Success) {
						this.StockEntity = resData.Data
						this.StockEntity.NewLocationCode=''
						this.newLocationFouce=true
					} else {
						uni.showModal({
							content: resData.Message,
							showCancel: false
						});
						return
					}

				},
				fail: () => {

				},
				complete: () => {

				}
			})

		},
		ConfirmDelete() {
			//uni.getStorageSync('')
			this.api.baseRequest({
				url:  'Stock/ConfirmDeleteStockByPda',
				method: "post",
				data: this.StockEntity,
				success: res => {
					var resData = JSON.parse(res.data.Content)
					if (resData.Success) {
						this.StockEntity = {
							NewLocationCode: '',
							LocationCode: '',
							MaterialLabel: ''
						}
					} else {
						uni.showModal({
							content: resData.Message,
							showCancel: false
						});
						this.StockEntity.NewLocationCode=''
						this.newLocationFouce=true
						
						return
					}

				},
				fail: () => {

				},
				complete: () => {

				}
			})

		},
		CancelDelete(){
			uni.navigateBack({
				
			})
		}
	}
}
